import { Component } from '@angular/core';

@Component({
  selector: 'app-parent',
  template: `
    <h3>Value: {{ value }}</h3>
    <app-child (upClick)="changeValue($event)"></app-child>
  `,
})
export class ParentComponent {
  value = 0;

  changeValue(isAdd: boolean) {
    if (isAdd) {
      this.value = this.value + 1;
    } else {
      this.value = this.value - 1;
    }
  }
}
