import { Component, ViewChild } from '@angular/core';
import { ChildSecondComponent } from './child-second.component';
@Component({
  selector: 'app-parent-second',
  template: `
    <app-child-second></app-child-second>
    <button (click)="onAddForChild()">Add for child</button>
  `,
})
export class ParentSecondComponent {
  @ViewChild(ChildSecondComponent)
  myChild: ChildSecondComponent;
  onAddForChild() {
    this.myChild.value++;
  }
}
