import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

@Injectable()
export class WeatherService {
  constructor(private http: Http) {}
  getTemp(cityName: string) {
    const url =
      'https://api.openweathermap.org/data/2.5/weather?appid=69259c7e5bfae649eab43065dc3f7dfa&units=metric&q=' +
      cityName;
    return this.http
      .get(url)
      .toPromise()
      .then((res) => res.json())
      .then((resJson) => resJson.main.temp);
  }
}
